import React from 'react'

import "./FormularioModal.css"

const FormularioModal = ( props ) => {

    const handleInputChange = event => {
        console.log(event)
        const { name, value } = event.target
        props.setInvitado({ ...props.invitado, [name]: value })
        console.log(props.invitado);
    }

    const handleCheckBoxChange = event => {
        //console.log(event.target.checked)
        const { name, checked } = event.target
        props.setInvitado({ ...props.invitado, [name]: (checked? '1' : '0') })
        //console.log(invitado);
    }      

    const handleRadioButtonChange = event => {
        //console.log(event.target.checked)
        const { name, value } = event.target
        //alert(value)
        props.setInvitado({ ...props.invitado, [name]: value })
        //console.log(invitado);
    }      


    return ( 
        <div className="form-group">

            {/* <header>
                <h1>
                    <a href="#">
                        <img src="http://tfgms.com/sandbox/dailyui/logo-1.png" alt="Authentic Collection"/>
                    </a>
                </h1>
            </header> */}
            <h1 class="text-center">Airam &amp; Noemi</h1>
            <label htmlFor="campo1">Nombres de los asistentes</label>
            <span>: &nbsp; </span>
            <input className="form-control" type="text"
                name="campo1" id="campo1"
                onChange={handleInputChange} value={props.invitado.campo1}
            />

            <hr size="5" width="100%" ></hr>  
            <label htmlFor="campo2">Alergias / Intolerancias / Vegano </label>
            <span>: &nbsp; </span>
            <input className="form-control"  type="text"
                name="campo2" id="campo2"
                onChange={handleInputChange} value={props.invitado.campo2}
            />

            <label htmlFor="campo3" style={{ marginTop: `7px` }}>Prop&oacute;n una canci&oacute;n para la fiesta</label>
            <span> &nbsp; </span>
            <input className="form-control" type="text"
                name="campo3" id="campo3"
                onChange={handleInputChange} value={props.invitado.campo3}
            /> 

            <label htmlFor="campo4" style={{ marginTop: `7px` }}>Prop&oacute;n una bebida</label>
            <input className="form-control" type="text"
                name="campo4" id="campo4"
                onChange={handleInputChange} value={props.invitado.campo4}
            /> 

            <label htmlFor="campo5" style={{ marginTop: `7px` }}>Observaciones o comentarios</label>
            <input className="form-control" type="text"
                name="campo5" id="campo5"
                onChange={handleInputChange} value={props.invitado.campo5}
            /> 

            <strong>Se requiere autobús (Salida desde Vecindario)&nbsp;</strong>
            <br></br>
            <label> 
                <input
                name="campo6" id="campo6"
                checked={props.invitado.campo6 === "No" ? true : false}
                type="radio"
                value="No"
                onChange={handleRadioButtonChange}
                />&nbsp;No
            </label>
            &nbsp;&nbsp;/&nbsp;&nbsp;
            <label> 
                <input
                name="campo6" id="campo6"
                checked={props.invitado.campo6 === "Si" ? true : false}
                type="radio"
                value="Si"
                onChange={handleRadioButtonChange}
                />&nbsp;Si
            </label>
            <hr size="5" width="100%" ></hr>  

            <strong>Por favor, confirmar asistencia antes del 15 de agosto de 2023</strong> 
            <p>Si desean enviarnos un mensaje lo pueden hacer al mail:&nbsp; <a href="mailto:info@lauravegaeventos.es" target="_blank">info@lauravegaeventos.es</a></p>
            
        </div>
     );
}
 
export default FormularioModal;